//直播公会赛 webapp 模块的api接口
export async function guildBaseApi ({ cmd, cmdName, data }) {
  return $httpPost({
    ns: namespace_guild,
    cmd,
    data,
    noencode: true,
    mapExt: {
      cmdName,
      ...comMapExt_guild
    }
  }).then(res => {
    return handleResponse(res, cmd);
  });
}
//直播资格赛 webapp 模块的api接口
export async function qualificationBaseApi ({ cmd, cmdName, data }) {
  return $httpPost({
    ns: namespace_qua,
    cmd,
    data,
    noencode: true,
    mapExt: {
      cmdName,
      ...comMapExt_qua
    }
  }).then(res => {
    return handleResponse(res, cmd);
  });
}

function handleResponse (res, cmd) {
  if (res.code === 0 && res.subcode === 0 && res.data && res.data[cmd]) {
    let data = res.data[cmd];
    data.isWebappSuccess = true;
    return data;
  } else {
    if (res.code === 997 || res.code === 996) {
      res.message = '网络错误';
    }
    let message = res.msg || res.message;
    if (message.indexOf('L5 get error!') >= 0) {
      message = '服务繁忙，请退出后重新进入页面。';
    }
    res.message = message;
    handleError(res);
    let cmdArr = cmd.split('.');
    hotClick(`code.${cmdArr[cmdArr.length - 1]}.` + res.code + message);
    return res;
  }
}
