<template>
  <nav class="nav_act nav_act__normal">
    <a
      class="nav_act__item"
      href="javascript:"
      v-for="item in NAV_RANK_TYPE"
      :key="item.value"
      :class="{ 'nav_act__item--current': item.value === pos }"
      @click="handleChange(item.value)"
    >
      <span class="nav_act__desc">{{ item.label }}</span>
    </a>
  </nav>
</template>

<script>
import { NAV_RANK_TYPE } from './../../../common/constants';

export default {
  name: 'Nav',
  data () {
    return {
      NAV_RANK_TYPE,
      pos: 0,
      rankType: 'king'
    };
  },
  watch: {
    $route: {
      immediate: true,
      handler (value) {
        if (this.rankType === value.query.rankType) {
          return;
        }
        this.rankType = value.query.rankType || 'king';
      }
    }
  },
  methods: {
    handleChange (value) {
      this.$hotClick(
        '主播榜单页.click.切换' + this.pos === 1 ? '贡献总榜' : '本轮贡献榜'
      );
      this.pos = value;
      this.$emit('change', this.pos);
    }
  }
};
</script>
