<template>
  <div class="page_header">
    <div class="guild_header__more">
      <a class="btn btn_more" @click="handleJump('rank')">完整榜单</a>
      <a class="btn btn_more" @click="handleJump('rule')">活动规则</a>
    </div>
    <div class="page_header__show">
      <img
        class="page_header__img"
        v-lazy="{
          src: guildIcon,
          error: $defaultFace,
          loading: $defaultFace
        }"
        alt=""
      />
    </div>
    <h3 class="page_header__tit" v-html="guildName"></h3>
    <div class="page_header__info">
      <span class="icon_nav" :class="getRankType().class"></span
      >{{ getRankType().label }}
    </div>
    <div class="page_header__desc">
      <strong>{{ totalStar }}</strong> 总闪耀值
    </div>
  </div>
</template>

<script>
import { RACE_GUILD, RACE_GUILD_RULE } from './../../../common/constants';
export default {
  name: 'Headers',
  props: {
    info: {
      type: Object,
      default: null
    }
  },
  data () {
    return {
      rankType: 'king'
    };
  },
  watch: {
    $route: {
      immediate: true,
      handler (value) {
        if (this.rankType === value.query.rankType) {
          return;
        }
        this.rankType = value.query.rankType || 'king';
      }
    }
  },
  computed: {
    guildIcon () {
      return this.info && this.info.strGuildIcon;
    },
    guildName () {
      return this.$parseEmoji((this.info && this.info.strGuildName) || '');
    },
    totalStar () {
      return this.$parseStarValue(this.info && this.info.llTotalStar);
    }
  },
  methods: {
    getRankType () {
      return {
        label: this.rankType === 'king' ? '王者赛道' : '钻石赛道',
        class: this.rankType === 'king' ? 'icon_king_s' : 'icon_diamond_s'
      };
    },
    handleJump (type = 'rank') {
      if (type === 'rule') {
        this.$hotClick('公会榜单页.click.跳转规则页');
        this.$router.replace({
          path: RACE_GUILD_RULE,
          query: {
            ...this.$route.query,
            zone: RACE_GUILD_RULE
          }
        });

        return;
      }
      this.$hotClick('公会榜单页.click.跳转榜单页');
      this.$router.replace({
        path: RACE_GUILD,
        query: {
          ...this.$route.query,
          zone: RACE_GUILD
        }
      });
    }
  }
};
</script>
