<template>
  <div>
    <Headers :info="guildInfo" />
    <Nav @change="handleChange" />
    <div class="box_main">
      <div class="box_main__bg">
        <div class="box_main__repeat"></div>
      </div>
      <div class="box_main__con">
        <div class="box_step" v-if="rankType === 0">
          <a class="box_step__btn"
            ><span class="icon" :class="getPrevStyle()" @click="handlePrev"
              >上一个</span
            ></a
          >
          <div class="box_step__con">
            <span class="box_step__status" v-if="isActState === 'end'"
              >已结束</span
            >
            <h4 class="box_step__tit" v-if="seasonList.length">
              {{ (seasonList[pos].label || '').replace('#', '') }}
            </h4>
            <p class="box_step__desc" v-if="seasonList.length">
              {{ seasonList[pos].time || '' }}
            </p>
          </div>
          <a class="box_step__btn"
            ><span class="icon" :class="getNextStyle()" @click="handleNext"
              >下一个</span
            ></a
          >
        </div>
        <div class="mod_blank" v-if="isActState === 'wait'">
          <img
            class="mod_blank__img"
            src="https://kg.qq.com/gtimg/mediastyle/mobile/event/20200928_guild_annual/img/blank.png"
            alt=""
          />
          <p class="mod_blank__desc">榜单暂未产生，敬请期待</p>
        </div>
        <ul
          class="qui_media qui_media--live"
          v-if="isActState === 'going' || isActState === 'end'"
        >
          <RankListItem
            v-for="(item, index) in list"
            :key="item.uid"
            :rank="index + 1"
            :info="item"
          />
        </ul>
        <a
          class="mod_more"
          v-if="isActState === 'going' || isActState === 'end'"
        >
          <span class="icon icon_more_l"></span>
          <span class="mod_more__info">{{
            hasMore ? '加载更多' : '暂无更多数据'
          }}</span>
          <span class="icon icon_more_r"></span>
        </a>
      </div>
    </div>
  </div>
</template>

<script>
import Headers from './../../components/guilds/detail/Headers';
import Nav from './../../components/guilds/detail/Nav';
import RankListItem from './../../components/guilds/detail/RankListItem';
import { HEADER_RANK_TYPE } from './../../common/constants';

import { getAnchorContributeList, getSeasonInfo } from './../../common/request';

export default {
  name: 'Detail',
  data () {
    return {
      HEADER_RANK_TYPE,
      pos: 1, // 当前轮次
      rankType: 0, // 0:轮次榜单，1:总榜
      list: [],
      curTime: Date.now() / 1000,
      curStage: 1,
      curRound: 1,
      seasonList: [],
      guildInfo: null,
      strPassBack: '',
      isActState: 'going',
      hasMore: true,
      timer: null
    };
  },
  computed: {},
  components: {
    Headers,
    Nav,
    RankListItem
  },
  watch: {
    '$route.query': {
      immediate: true,
      handler (value) {
        if (!value) return;
        this.updateGuildInfo();
      }
    }
  },
  async mounted () {
    const { from = '' } = this.$route.query;
    this.$hotClick(
      `主播榜单页.pv.${
        from === '1' ? '主播卡片' : from === '1' ? '分享入口' : '运营位入口'
      }`
    );
    window.addEventListener('scroll', this.handleScroll, true);
  },
  methods: {
    async updateGuildInfo () {
      this.clear();
      const { round = 1, stage = 1 } = this.$route.query;
      const resData = await getSeasonInfo({
        uQuerySeasonId: 0 // 占位，默认传0，请求当前赛季信息
      });
      this.curStage = Number(stage) || 1;
      this.curRound = Number(round) || 1;
      this.curTime = resData.uCurTime || Date.now() / 1000;
      this.seasonList =
        resData.vctSeasonInfo.map((item, index) => {
          if (
            item.uRoundId === this.curRound &&
            item.uStageId === this.curStage
          ) {
            this.pos = index;
          }
          return {
            ...item,
            label: item.strSeasonTitle,
            time: `${this.$format(
              item.uBeginTs,
              'MM/DD HH:mm:ss'
            )}-${this.$format(item.uEndTs, 'MM/DD HH:mm:ss')}`
          };
        }) || [];
      this.getData({
        stage,
        round
      });
    },
    async getData ({ stage, round }) {
      const { guildId = 0 } = this.$route.query;
      const resData = await getAnchorContributeList({
        uStageId: Number(stage),
        uRoundId: Number(round),
        uGuildId: Number(guildId),
        uRankType: this.rankType,
        strPassBack: this.strPassBack
      });
      this.strPassBack = resData.strPassBack;
      this.hasMore = resData.bHasMore;
      this.guildInfo = resData.stCurGuildInfo;
      this.list = this.$filterMultiValue(
        this.list,
        resData.vctRankList,
        'uAnchorId'
      );
    },
    handleChange (value) {
      this.clear();
      this.rankType = value;
      this.changeRound();
    },
    changeRound () {
      let data = this.seasonList[this.pos];
      if (!data) return;
      if (this.curTime > data.uEndTs) {
        // 已结束
        this.isActState = 'end';
      } else if (this.curTime < data.uBeginTs) {
        // 未开始
        this.isActState = 'wait';
      } else {
        this.isActState = 'going';
      }
      this.getData({
        stage: data.uStageId,
        round: data.uRoundId
      });
    },
    handlePrev () {
      if (this.pos <= 0) return;
      this.clear();
      this.pos--;
      this.changeRound();
    },
    handleNext () {
      if (this.pos >= this.seasonList.length - 1) return;
      this.clear();
      this.pos++;
      this.changeRound();
    },
    handleScroll () {
      this.timer = this.$handleScroll(this.timer, () => {
        if (this.hasMore) {
          this.changeRound();
        }
      });
    },
    getPrevStyle () {
      return this.pos <= 0 ? 'icon_front_off' : 'icon_front_on';
    },
    getNextStyle () {
      return this.pos >= this.seasonList.length - 1
        ? 'icon_behind_off'
        : 'icon_behind_on';
    },
    clear () {
      if (this.timer) {
        clearTimeout(this.timer);
        this.timer = null;
      }
      this.strPassBack = '';
      this.hasMore = true;
      this.guildInfo = null;
      this.$set(this, 'list', []);
    }
  }
};
</script>
